.cart__body {
  margin-bottom: 0.7em;
  font-family: 'Libre Baskerville', serif;
}

.cart-item {
  margin-bottom: 0.3em;
  display: flex;
  justify-content: space-between;
  font-family: 'Libre Baskerville', serif;
  align-items: center;
}

.cart-item__name {
  margin-left: 0.5em;
  vertical-align: middle;
  font-family: 'Libre Baskerville', serif;
}

.cart-item__price {
  font-weight: bold;
  font-family: 'Libre Baskerville', serif;
}

.cart__total {
  font-weight: bold;
  font-size: 1.5em;
  line-height: 1.1em;
  text-align: right;
  font-family: 'Libre Baskerville', serif;
}

.product {
    margin: 0;
    overflow: auto;
    font-family: 'Libre Baskerville', serif;
}

.product__price {
    display: inline-block;
    line-height: 34px;
    font-family: 'Libre Baskerville', serif;
}

.product__button-wrap {
    float: right;
    font-family: 'Libre Baskerville', serif;
}

.product-list {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    font-family: 'Libre Baskerville', serif;
}

.product-list__item {
    list-style: none;
    width: 32%;
    font-family: 'Libre Baskerville', serif;
}

